
import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { ProductStockService } from 'src/app/service/product-stock.service';
import { DataService } from 'src/app/service/data.service';


@Component({
  selector: 'app-product-stock-list',
  templateUrl: './product-stock-list.component.html'
})
export class ProductStockListComponent implements OnInit {

  PageTitle = "Product Stock"

  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  showPagination: boolean = false;
  timezone: any;
  time_format: any;
  date_format: string = "M/d/yy";
  currentCompany: any;

  //For Dynamic List
  headerData: Array<any> = [];

  constructor(
    private paginationService: PaginationService,
    private productStockService: ProductStockService,
    private dataService: DataService
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['product_stock'];
    // });
    this.dataService.currentCompany.subscribe((response) => {
      if (response) {
        this.timezone = String(response.timezone.format);;
        this.time_format = response.time_format.label;
        this.date_format = response.date_format.label;
        this.currentCompany = response;
      }
    });
    this.loadform = false;
    this.getObjects();
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.productStockService.getProductStockList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {
        this.objectArray = response.data.list ? response.data.list : [];
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }

  calcDate(date1): String {
    date1= new Date(date1);
    if (date1) {
      let date2 = new Date()
      let diff = Math.floor(date2.getTime() - date1);
      let day = 1000 * 60 * 60 * 24;
      let ndays = Math.floor(diff / day);
      let years = Math.floor(ndays / 365);
      ndays = ndays - (365 * years);
      let months = Math.floor(ndays / 30);
      let days = ndays - Math.floor(months * 30);
      let message = '';
      if (days && days != undefined && days != null) {
        message = Math.abs(days) + " days ";
      }
      if (months) {
        message += Math.abs(months) + " months ";
      }
      if (years) {
        message += Math.abs(years) + " years ";
      }
      if (message) {
        message += "ago \n";
        return 'Last  ' + message;
      }else{
        return "Today";
      }
    }
  }

}




